<template lang="pug">
    div.user-broadcast-content
        div.item-state
            div.playback(v-text="`${count}个精彩回放`")
            div.state
                span.newest(:class="{'active' : sort==0}" @click="doSort(0)") 最新
                span.the-hot(:class="{'active' : sort==1}" @click="doSort(1)") 最热
        mt-loadmore(
            :top-method="loadTop"
            :bottom-method="loadBottom"
            :bottom-all-loaded="allLoaded"
            :autoFill="autoFill"
            ref="loadmore"
            @top-status-change="handleTopChange"
            @bottom-status-change="handleBottomChange"
        )
            mint-loadmore-top(:topStatus="topStatus" slot="top")
            template(v-if="isLoaded")
                list-playback(
                    :list="list"
                    :allLoaded="allLoaded"
                )
            mint-loadmore-bottom(:bottomStatus="bottomStatus" slot="bottom" v-show="!allLoaded")
</template>
<script>
import listPlayback from '@/components/listPlayback'
import mintLoadmoreTop from '@/components/mintLoadmoreTop'
import mintLoadmoreBottom from '@/components/mintLoadmoreBottom'
export default {
    name: 'toolLiveBroadcast',
    components: {
        listPlayback,
        mintLoadmoreTop,
        mintLoadmoreBottom
    },
    data() {
        return {
            list: '',
            page: 2,
            has_next: '',
            autoFill: false,
            allLoaded: false,
            count: 0,
            sort: 0,
            isLoaded: false,
            topStatus: '',
            bottomStatus: ''
        }
    },
    created() {
        this.getData(this.sort);
    },
    methods: {
        doSort(sort) {
            this.getData(sort);
        },
        getData(sort, isRefresh = false) {
            // Indicator.open();
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/userUser_review' : this.api.userUser_review();
            this.axios.get(axiosUrl, {
                params: {
                    sort: sort,
                    to_user_id: this.$route.query.to_user_id
                }
            }).then(res => {
                // Indicator.close();
                this.isLoaded = true;
                this.sort = sort;
                if (res.data.status == 1) {
                    this.list = res.data.list;
                    this.count = res.data.count;
                    this.has_next = res.data.has_next;
                    this.page = 2;
                }
                else {
                    this.Toast(res.data.error || '操作失败');
                }
                this.$nextTick(() => {
                    isRefresh && this.$refs.loadmore.onTopLoaded();
                    this.$emit('init');
                });
            })
                .catch(err => {
                    this.Toast(err || '网络异常，请求失败');
                    isRefresh && this.$refs.loadmore.onTopLoaded();
                });
        },
        loadTop() {
            this.getData(this.sort, true);
        },
        loadBottom() {
            // 无限加载
            let self = this;
            if (self.has_next) {
                let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/userUser_review' : this.api.userUser_review();
                self.axios.get(axiosUrl, {
                    params: {
                        p: self.page,
                        to_user_id: self.$route.query.to_user_id,
                        sort: self.sort
                    }
                }).then(res => {
                    setTimeout(() => {
                        if (res.data.status == 1) {

                            self.has_next = res.data.has_next;
                            self.page = self.page + 1;

                            if (res.data.list && res.data.list.length && res.data.list.length > 0) {
                                for (let i = 0; i < res.data.list.length; i++) {
                                    self.list.push(res.data.list[i]);
                                }
                            }
                        }
                        else {
                            this.Toast(res.data.error || '操作失败');
                        }
                        this.$nextTick(() => {
                            self.$refs.loadmore.onBottomLoaded();
                            self.$emit('init');
                        })
                    }, 500);
                })
                    .catch(err => {
                        this.Toast(err || '网络异常，请求失败');
                        self.$refs.loadmore.onBottomLoaded();
                    });
            }
            else {
                return false;
            }
        },
        handleTopChange(status) {
            // 获取下拉最新状态
            this.topStatus = status;
        },
        handleBottomChange(status) {
            // 获取上拉最新状态
            this.bottomStatus = status;
        }
    },
    watch: {
        list() {
            // 当页面数据发生改变,进行处理
            this.allLoaded = !Boolean(this.has_next);
        }
    }
}
</script>
<style lang="less" scoped>
@import "../../../assets/css/variable.less";
.user-broadcast-content {
    .item-state {
        height: 2.45rem;
        width: 100%;
        padding: 0 .5rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: 1px solid #e5e5e5;
        background-color: #f7f7f7;
        .playback {
            color: @fc-main;
            font-size: .65rem;
        }
        .state {
            font-size: .65rem;
            .newest {
                color: @fc-light;
                &.active {
                    color: @fc-main;
                }
            }
            .the-hot {
                color: @fc-light;
                margin-left: 1rem;
                &.active {
                    color: @fc-main;
                }
            }
        }
    }
}
</style>




